import { useEffect, useRef, useState } from "react";
import { NewMessageForm, Messages } from "./components/messageForm";
function App() {
  const [messages, setMessages] = useState({})
  const [showMessages, setShowMessages] = useState(false)
  const [showNewMessage, setShowNewMessage] = useState(false)
  const writeListenerMessage = data => {
    const timestamp = Date.now()
    console.log("data to be written: " + data.message + " " + data.username + " " + data.id)
    setMessages(prevMessages => {
      console.log("setmessages: ")
      let newMessages = {
        ...prevMessages
      }
      newMessages[timestamp] = data
      console.log(newMessages)
      return newMessages    
    })
  }

  const generateMessagesViewBody = () => {
    const data = Object.keys(messages).map((elem) => {
        const elemKey = elem 
        console.log("messages: unix timestamp", elemKey)
        const date = new Date(Number(elemKey))
        console.log("messages: timestamp: date", date)
        const hour = date.getHours()
        console.log("messages: timestamp: hour", hour)
        const minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
        console.log("messages: timestamp: minutes", minutes)
        const time = hour + ":" + minutes

        return (
            <div
                key={elemKey}
            >
                <div className='voxMessage'>
                    {messages[elemKey]["message"]}
                </div>
                <div className='voxTimestamp'>
                    {time}
                </div>
            </div>
        )
    })

    return (
        <div className='voxModalMessagesBody'>
            <div className='messageTitle'>
               Questions/Messages 
            </div>
            {data}
        </div>
    )

  }

  const handleMessagesViewClose = () => {
    setShowMessages(false)
  }
  
  const handleMessagesViewShow = () => {
    setShowMessages(true)
  }
  
  const handleNewMessageViewClose = () => {
    setShowNewMessage(false)
  }
  
  const handleNewMessageViewShow = () => {
    setShowNewMessage(true)
  }
  
  return (
    <div >
      <button onClick={handleMessagesViewShow}>Show Messages</button>
      <NewMessageForm show={showNewMessage} close={handleNewMessageViewClose} writeListenerMessage={writeListenerMessage}/>
      <Messages show={showMessages} close={handleMessagesViewClose} body={generateMessagesViewBody()} handleNewMessageViewShow={handleNewMessageViewShow}/>
    </div>
  );

  
}

export default App;