// import './messageForm.css'
import '../Player.css'
import logo from '../images/vox_logo.svg';
import closebtn from '../images/red-x-icon.png';
import editSvg from '../images/edit_white_24dp.svg'
import { AnimatePresence, motion } from 'framer-motion';

const voxModalWrapperAnimation = {
    visible: { opacity: 1, y: 0 },
    hidden: { opacity: 0, y: "100%" }
}

export const NewMessageForm = ({show ,close, writeListenerMessage}) =>{
    const submitForm = (e)=>{
        e.preventDefault()
        const formData = new FormData(e.target)
        const payload = Object.fromEntries(formData)
        console.log("messages new: senderName and message", payload)
        const listenerId = sessionStorage.getItem('VoxConnectListenerId')
        const sendername = sessionStorage.setItem("voxSenderName", payload.senderName)
        const data = {
            "id": listenerId, // <- User Id ( random UUID )
            "message": payload.senderMessage,
            "username": payload.senderName
        }
        writeListenerMessage(data)
        close()
    }
    const sendername = sessionStorage.getItem("voxSenderName")
    return (
    <AnimatePresence>
        {show && (
        <motion.div className='voxModalWrappper'
            variants={voxModalWrapperAnimation}
            initial="hidden"
            animate="visible"
            transition={{ ease: "easeIn", duration: 0.2 }}
            exit={{ opacity: 0, y: "100%", transition: { ease: "easeIn", duration: 0.2 } }}
            key="voxNewMessageModal"
        >
            <button onClick={() => close()} className='closeVoxModal'>
                <img src={closebtn} className="closeBtn" />
            </button>
            <div className='voxModalBackground'>
                <div className='voxModalTitle'>
                    <img src={logo} className='modaltitle' />
                </div>
                <div className='voxModalMessagesContainer'>
                    <div className='voxModalMessagesBody'>
                        <div className='messageTitle'>
                        New Questions/Comments 
                        </div>
                        <form className='createMessage' onSubmit={submitForm}>
                            <div className='messageName'>
                                <label>Name</label>
                                <input
                                    type="text"
                                    defaultValue={sendername}
                                    name='senderName'
                                    required
                                />
                            </div>
                            <div className='messageText'>
                                <label>Message</label>
                                <textarea
                                    required
                                    rows={10}
                                    name='senderMessage'
                                ></textarea>
                            </div>
                            <div className='sendMessageOuter'>
                                <button className='sendMessage' type='submit'>Send</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </motion.div>
        )}
    </AnimatePresence>
    )
}

export const Messages = ({show, close, body, handleNewMessageViewShow}) => {
    return (
        <AnimatePresence>
            {show && (
            <motion.div className='voxModalWrappper'
                key="voxMessagesModal"
                variants={voxModalWrapperAnimation}
                initial="hidden"
                animate="visible"
                transition={{ ease: "easeIn", duration: 0.2 }}
                exit={{ opacity: 0, y: "100%", transition: { ease: "easeIn", duration: 0.2 } }}  
            >
                <button onClick={() => close()} className='closeVoxModal'>
                    <img src={closebtn} className="closeBtn" />
                </button>
                <div className='voxModalBackground'>
                    <div className='voxModalTitle'>
                        <img src={logo} className='modaltitle' />
                    </div>
                    <div className='voxModalMessagesContainer'>
                        {body}
                    </div>
                </div>
                <div className='newMessage' onClick={handleNewMessageViewShow}>
                    <img src={editSvg} />
                    New
                </div>
            </motion.div>
            )}
        </AnimatePresence>
    )
}